/* Signup Css */
.signup-container {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
  background-color: #f0f0f0; 
}

.signup-card {
  display: flex;
  width: 800px;
  height: 480px;
  background-color: white;
  border-radius: 10px;
  box-shadow: 0 8px 16px rgba(0, 0, 0, 0.2);
  overflow: hidden;
}

.signup-left {
  background-image: url('https://i.pinimg.com/736x/5d/e0/8d/5de08de24459fedac3d28b10a039e2a6.jpg');
  flex: 1;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  color: rgb(255, 255, 255);
  padding: 20px;
}

.signup-left h2 {
  font-size: 30px;
  margin-bottom: 20px;
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
}

.signup-left p {
  font-size: 14px;
  margin-bottom: 20px;
  text-align: center;
  margin-top: -10px;
}

.signup-left button {
  background-image: url('https://i.pinimg.com/736x/5d/e0/8d/5de08de24459fedac3d28b10a039e2a6.jpg');
  color: #f0fafc;
  border: 2px solid white;
  padding: 12px;
  border-radius: 25px;
  cursor: pointer;
  font-size: 16px;
  width: 60%;

}

.signup-left button:hover {
  color: #002747;
}


.signup-right {
  flex: 1.2;
  padding: 40px;
}

.signup-right h2 {
  font-size: 31px;
  margin-bottom:  45px;
  color: #7fb6e4;
  text-align: center;
  margin-top: -5px;
}


.signup-right .form-group {
  margin-bottom: 15px;
  position: relative; }

.signup-right label {
  font-size: 14px;
 
  margin-bottom: 5px;
}

.signup-right input,
.signup-right textarea,
.signup-right select {
  width: 78%;
  padding: 10px 40px; 
  border: 1px solid #ddd;
  border-radius: 5px;
  margin-top: 5px;
  outline: none;
  transition: border-color 0.3s ease;
}

.signup-right input:focus,
.signup-right textarea:focus,
.signup-right select:focus {
  border-color: #7fb6e4;
  box-shadow: 0 0 5px #7fb6e4;
}


.signup-right .form-group i {
  position: absolute;
  left: 10px; 
  top: 50%;
  transform: translateY(-50%);
  color: #888;
  font-size: 18px;
  pointer-events: none; 
}


.signup-right .form-group .input-wrapper {
  position: relative; 
}

.signup-right .form-group select {
  width: 100%;
  padding: 10px 40px; 
  border: 1px solid #ddd;
  border-radius: 5px;
  margin-top: 5px;
  outline: none;
  transition: border-color 0.3s ease;
}

.signup-right .form-group select:focus {
  border-color: #7fb6e4;
  box-shadow: 0 0 5px #7fb6e4;
}

.signup-right .form-group .input-wrapper i {
  position: absolute;
  left: 10px; 
  top: 50%;
  transform: translateY(-50%);
  color: #888;
  font-size: 18px;
  pointer-events: none; 
}

.signup-right button {
  background-image: url('https://i.pinimg.com/736x/5d/e0/8d/5de08de24459fedac3d28b10a039e2a6.jpg');
  color: #f0fafc;
  border: 2px solid white;
  padding: 12px;
  border-radius: 25px;
  cursor: pointer;
  font-size: 16px;
  width: 60%;
  margin-left: 70px;
}

.signup-right button:hover {
  color: #002050;
}
