/* Employer's Dashboard Css */
.employer-dashboard {
  font-family: Arial, sans-serif;
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}


.employer-dashboard-navbar {
  background-color: #fff;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 5px 20px;
  box-shadow: 0px 2px 5px rgba(0, 0, 0, 0.1);
  position: relative;
}

.employer-dashboard-navbar-content {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
}

.employer-dashboard-company-name {
  font-size: 1.8em;
  font-weight: bold;
  text-align: center;
  flex-grow: 1;
  color:#7fb6e4;
  margin-left: -82%;
}

.employer-dashboard-logout-button {
  background-color: #7fb6e4;
  color: white;
  padding: 2px 10px;
  font-size: 1em;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

.employer-dashboard-logout-button:hover {
  background-color: #7fb6e4;
}


.employer-dashboard-background {
  background-image: url('https://i.pinimg.com/736x/5d/e0/8d/5de08de24459fedac3d28b10a039e2a6.jpg');
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  padding: 40px 20px;
  text-align: center;
  height: 200px;
  display: flex;
  justify-content: center;
  align-items: center;
}

.employer-dashboard-header h1 {
  font-size: 2.5em;
  font-weight: bold;
  color: #fff; 
  margin-bottom: 10px;
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
}

.employer-dashboard-header p {
  font-size: 1.2em;
  color: #f0f0f0; 
  margin: 5px 0;
}


.box {
  background-color: #f8f9fa;
  border-radius: 8px;
  padding: 15px;
  box-shadow: 0px 2px 10px rgba(11, 1, 109, 0.1) !important;
  transition: transform 0.2s ease, box-shadow 0.2s ease;
  width: 90%;
  max-width: 500px;
  text-align: left;
  position: relative;
  cursor: pointer;
}


.box:hover {
  box-shadow: 0px 4px 15px rgba(65, 11, 190, 0.2) !important; 
}


.box .fa-pencil-alt {
  cursor: pointer;
  font-size: 1.2em;
  color: grey;
  position: absolute;
  top: 10px;
  right: 10px;
  transition: color 0.3s ease;
}

.box:hover .fa-pencil-alt {
  color: #0056b3;
}

.box:hover {
  transform: translateY(-5px);
  box-shadow: 0px 4px 15px rgba(0, 0, 0, 0.15);
}


.employer-dashboard-details {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 20px;
  padding: 20px;
  justify-items: center;
}

.employer-dashboard-detail-box h2 {
  font-size: 1.5em;
  color: #7fb6e4;
  margin-bottom: 10px;
}

.employer-dashboard-detail-box p,
.employer-dashboard-detail-box input {
  font-size: 1.1em;
  color: #555;
  border: none;
  width: 100%;
}

.employer-dashboard input[type="text"],
.employer-dashboard button {
  font-size: 1.1em;
  padding: 10px;
  border-radius: 5px;
}

.employer-dashboard input[type="text"] {
  border: 1px solid #ccc;
  width: 100%;
  box-sizing: border-box;
}

.employer-dashboard input[type="text"]:focus {
  border-color: #007bff;
}

.employer-dashboard button {
  background-color: #7fb6e4;
  color: white;
  border: none;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

.employer-dashboard button:hover {
  background-color: #0056b3;
}


/* Responsive Layout */
@media (max-width: 768px) {
  .employer-dashboard-details {
      grid-template-columns: 1fr;
  }

  .employer-dashboard-company-name {
      font-size: 1.5em;
  }

  .box {
      width: 100%;
  }
}
